$icons: (0:0)
$icons: map-merge($icons,(codedby: (X: 0px, Y:-73px, W: 47px, H: 5px, TW: 128px, TH: 98px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(coderiver: (X: -73px, Y:-59px, W: 49px, H: 5px, TW: 128px, TH: 98px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(memo-mini: (X: 0px, Y:-82px, W: 16px, H: 16px, TW: 128px, TH: 98px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(memo-small: (X: -73px, Y:0px, W: 55px, H: 55px, TW: 128px, TH: 98px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(memo: (X: 0px, Y:0px, W: 69px, H: 69px, TW: 128px, TH: 98px, IMG: '../img/icons.png')))


// Gets an attribute from the sass map
@function icon-attr($icon, $attr)
	$icon: map-get($icons, $icon)
	@return map-get($icon, $attr)

@mixin sprite($iconName)
	background-image: url(icon-attr($iconName, IMG))
	width: icon-attr($iconName, W)
	height: icon-attr($iconName, H)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)

@mixin sprite-retina($iconName)
	background-image: url(icon-attr($iconName, IMG))
	$width: icon-attr($iconName, W)
	$height: icon-attr($iconName, H)
	width: $width/2
	height: $height/2
	$x: icon-attr($iconName, X)
	$y: icon-attr($iconName, Y)
	background-position: $x/2 $y/2
	$tw: icon-attr($iconName, TW) 	
	$th: icon-attr($iconName, TH)
	background-size: $tw/2 $th/2  	


@mixin s($i)
	+sprite($i)

@mixin sr($i)
	+sprite-retina($i)	

